.App {
  font-family: sans-serif;
  text-align: center;
  color: white;
}

#cuadro {
  position: relative;
  width: 200px;
  height: 200px;
  background-color: black;
  animation: peque 3s infinite;
  cursor: pointer;
  padding: 50px;
  text-align: center;
}

#hola {
  margin-top: 60px;
  margin-bottom: 35px;
}

@keyframes peque {
  0% {
    top: 30px;
    left: 30px;
    width: 200px;
    height: 200px;
    border-radius: 50px;
    background-color: rgb(252, 169, 183);
  }
  100% {
    top: 0px;
    left: 0px;
    width: 300px;
    height: 300px;
    border-radius: 200px;
    background-color: rgb(53, 63, 95);
  }
}
